<template>
  <div id="app" :class="appClass">
    <Player />
    <router-view />
  </div>
</template>

<script>
  import {
    mapState,
    mapGetters
  } from 'vuex'
  import Player from "@/components/Player"

  export default {
    name: "app",
    components: {
      Player
    },
    mounted() {
      window.addEventListener('beforeunload', () => {
        this.$socket.close()
      })
    },
    computed: {
      appClass() {
        return {
          'show-player': this.showPlayer
        }
      },
      showPlayer() {
        if (this.connected && this.username != null && this.room != null && this.nowPlayingIndex != -1) {
          return true
        }
        return false
      },
      ...mapState([
        'connected',
        'username',
        'room'
      ]),
      ...mapGetters([
        'nowPlayingIndex'
      ]),
    }
  }
</script>

<style>
  #app.show-player {
    padding-bottom: 100px;
  }
  
  .container>.card:first-child {
    margin-top: 16px;
    margin-bottom: 8px;
  }
  
  .container>.card:not(:first-child) {
    margin-top: 8px;
    margin-bottom: 8px;
  }

  .card {
    box-shadow: none !important;
  }
</style>