<template>
    <mu-flex style="height: 100%;" fill align-content="center" align-items="center" align-self="center"
        justify-content="center">
        <mu-flex v-if="username != null" style="margin-right: 8px;">
            <mu-tooltip :content="username">
                <mu-chip class="auto-adapt-chip">
                    <mu-avatar :size="32">
                        <mu-icon value="account_circle"></mu-icon>
                    </mu-avatar>
                    <span class="span">{{ username }}</span>
                </mu-chip>
            </mu-tooltip>
        </mu-flex>
        <mu-flex>
            <mu-tooltip :content="connected ? '已连接' : '未连接，点击重新连接'">
                <mu-chip class="auto-adapt-chip" @click="retryConnect" :color="connected ? 'green' : 'red'">
                    <mu-avatar :size="32" :color="connected ? 'green' : 'red'">
                        <mu-icon :value="connected ? 'check' : 'close'"></mu-icon>
                    </mu-avatar>
                    <span class="span">{{ connected ? "已连接" : "未连接，点击尝试重新连接" }}</span>
                </mu-chip>
            </mu-tooltip>
        </mu-flex>
    </mu-flex>
</template>

<script>
    import {
        mapState
    } from "vuex"

    import {
        getWindowWidth
    } from "@/utils/utils"

    export default {
        name: "connectStatus",
        data() {
            return {
                windowWidth: getWindowWidth(),
                openTooltip: false
            }
        },
        methods: {
            retryConnect() {
                if (!this.connected) {
                    window.location.reload()
                }
            }
        },
        watch: {
            openTooltip() {
                if (this.windowWidth > 768) {
                    this.openTooltip = false
                }
            }
        },
        mounted() {
            window.addEventListener("resize", () => {
                this.windowWidth = getWindowWidth()
            })
        },
        computed: {
            ...mapState([
                'connected',
                'username'
            ])
        }
    }
</script>

<style>
    @media screen and (max-width: 768px) {
        .auto-adapt-chip {
            padding: 0 !important;
        }

        .auto-adapt-chip .span {
            display: none !important;
        }

        .auto-adapt-chip .mu-avatar:first-child {
            margin: 0 !important;
        }
    }
</style>