import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import VueSocketIO from "vue-socket.io";
import MuseUI from "muse-ui";
import "muse-ui/dist/muse-ui.css";

Vue.use(MuseUI);

String.prototype.myTrim = function(char, type) {
  if (char) {
    if (type == "left") {
      return this.replace(new RegExp("^\\" + char + "+", "g"), "");
    } else if (type == "right") {
      return this.replace(new RegExp("\\" + char + "+$", "g"), "");
    }
    return this.replace(
      new RegExp("^\\" + char + "+|\\" + char + "+$", "g"),
      ""
    );
  }
  return this.replace(/^\s+|\s+$/g, "");
};

Vue.use(
  new VueSocketIO({
    debug: true,
    connection: "http://154.81.3.56:3000/",
    vuex: {
      store,
      actionPrefix: "SOCKET_",
      mutationPrefix: "SOCKET_"
    }
  })
);

Vue.config.productionTip = false;

router.beforeEach((to, _from, next) => {
  if (to.meta.requireLogin) {
    next(vm => {
      if (vm.$store.state.username == null) {
        vm.$router.replace({
          name: "login",
          query: {
            fr: vm.$route.path
          }
        });
      }
    });
  } else {
    next();
  }
});

new Vue({
  router,
  store,
  mounted() {
    let requireLogin = router.matcher.match(window.location.hash.substring(1))
      .meta.requireLogin;
    if (requireLogin) {
      this.$router.replace({
        name: "login",
        query: {
          fr: window.location.hash.substring(1)
        }
      });
    }
  },
  render: h => h(App)
}).$mount("#app");
