<template>
    <div>
        <mu-appbar :z-depth="0" textColor="black" color="white">
            一起听歌
            <mu-flex style="height: 100%;" fill align-content="center" align-items="center" align-self="center"
                justify-content="center" slot="right">
                <mu-flex v-if="connected && username != null">
                    已登录：{{ username }}
                </mu-flex>
                <connect-status />
            </mu-flex>
        </mu-appbar>
    </div>
</template>

<script>
    export default {
        name: "blank",
        sockets: {
            logined() {
                this.$router.replace("/home")
            }
        },
        beforeRouteEnter(to, from, next) {
            next(vm => {
                if (vm.$store.state.username == null) {
                    if (window.localStorage.username == null) {
                        vm.$router.replace("/login")
                    } else {
                        vm.$socket.emit("login", window.localStorage.username)
                    }
                } else {
                    vm.$router.replace("/home")
                }
            })
        }
    }
</script>

