<template>
    <div v-if="showPlayer" class="player">
        <Aplayer @play="onPlay" @pause="onPause" @ended="onEnded" ref="aplayer" autoplay :listFolded="true"
            :showLrc="true" :music="music" />
    </div>
</template>

<script>
    import Aplayer from "vue-aplayer"
    import {
        mapState,
        mapGetters
    } from 'vuex'

    export default {
        data() {
            return {
                timer: null,
                inited: false,
                playingItem: null
            }
        },
        name: "player",
        components: {
            Aplayer
        },
        beforeDestroy() {
            clearInterval(this.timer);
        },
        watch: {
            showPlayer(newVal) {
                if (newVal) {
                    /*
                    this.$nextTick(() => {
                        this.$refs.aplayer.onProgressDragBegin = () => {}
                        this.$refs.aplayer.onProgressDragging = () => {}
                        this.$refs.aplayer.onProgressDragEnd = () => {}
                    })
                        */
                }
            },
            nowPlayingPosition(newVal, oldVal) {
                this.$nextTick(() => {
                    if (this.inited && (Math.abs(newVal - oldVal) >= 10 || (oldVal == 0 && newVal != 0))) {
                        this.$refs.aplayer.audio.currentTime = newVal
                    }
                })
            },
            nowPlayingIndex() {
                this.inited = false
            },
            nowPlayingItem(newVal, oldVal) {
                if (oldVal == null || (newVal != null && newVal.id != oldVal.id)) {
                    this.playingItem = newVal;
                    this.$nextTick(() => {
                        this.$refs.aplayer.audio.play();
                        this.$refs.aplayer.audio.currentTime = 0;
                    })
                }
            }
        },
        mounted() {
            this.timer = setInterval(() => {
                if (this.$refs.aplayer != null && this.inited && !this.$refs.aplayer.isLoading && this.$refs.aplayer.isPlaying && this.$refs.aplayer.playStat.playedTime >= 10 &&
                    this.nowPlayingIndex != -1) {
                    this.$socket.emit('report_progress', {
                        index: this.nowPlayingIndex,
                        isPlaying: this.$refs.aplayer.isPlaying,
                        playEnded: this.$refs.aplayer.playStat.playedTime == this.$refs.aplayer.playStat
                            .duration,
                        position: this.$refs.aplayer.playStat.playedTime,
                        duration: this.$refs.aplayer.playStat.duration
                    })
                }
            }, 2500)
        },
        methods: {
            onPlay() {
                if (this.nowPlayingPosition - this.$refs.aplayer.playStat.playedTime != 10) {
                    this.$refs.aplayer.audio.currentTime = this.nowPlayingPosition
                }
                this.$socket.emit('start_play', {
                    index: this.nowPlayingIndex
                })
                this.$nextTick(() => {
                    this.inited = true
                })
            },
            onPause() {
                console.log("onPause")
                this.$socket.emit('pause_play', {
                    index: this.nowPlayingIndex,
                    position: this.$refs.aplayer.playStat.playedTime,
                    duration: this.$refs.aplayer.playStat.duration
                })
            },
            onEnded() {
                this.$socket.emit('ended_play', {
                    index: this.nowPlayingIndex,
                    position: this.$refs.aplayer.playStat.playedTime,
                    duration: this.$refs.aplayer.playStat.duration
                })
            }
        },
        computed: {
            showPlayer() {
                if (this.connected && this.username != null && this.room != null && this.playingItem != null) {
                    return true
                }
                return false
            },
            ...mapState([
                'playList',
                'nowPlaying',
                'connected',
                'username',
                'room'
            ]),
            ...mapGetters([
                'nowPlayingIndex',
                'nowPlayingItem',
                'nowPlayingPosition'
            ]),
            music() {
                if (this.playingItem == null) {
                    return null
                }
                return {
                    src: this.playingItem.url,
                    title: this.playingItem.name,
                    artist: this.playingItem.artists,
                    pic: this.playingItem.pic,
                    lrc: this.playingItem.lrc
                }
            }
        }
    }
</script>

<style>
    .player {
        position: fixed;
        bottom: 0;
        left: 0;
        z-index: 10;
        width: 100%;
    }

    .aplayer-bar-wrap {
        pointer-events: none;
    }
</style>