<template>
    <div>
        <mu-appbar :z-depth="0" textColor="black" color="white">
            一起听歌
            <mu-flex style="height: 100%;" fill align-content="center" align-items="center" align-self="center"
                justify-content="center" slot="right">
                <mu-flex v-if="connected && username != null">
                    已登录：{{ username }}
                </mu-flex>
                <connect-status />
            </mu-flex>
        </mu-appbar>
        <mu-container class="container">
            <mu-card class="card">
                <mu-card-title title="欢迎使用" sub-title="Welcome"></mu-card-title>
                <mu-card-text>
                    <mu-form @submit.prevent.stop="login" ref="form" :model="form">
                        <mu-form-item label="用户名" prop="username">
                            <mu-text-field v-model="form.username" prop="username"></mu-text-field>
                        </mu-form-item>
                    </mu-form>
                </mu-card-text>
                <mu-card-actions>
                    <mu-button color="primary" @click="login">进入</mu-button>
                </mu-card-actions>
            </mu-card>
        </mu-container>
    </div>
</template>

<script>
    import ConnectStatus from "@/components/ConnectStatus"
    import {
        mapState
    } from 'vuex'

    export default {
        name: "login",
        components: {
            ConnectStatus
        },
        computed: {
            ...mapState([
                'connected',
                'username'
            ])
        },
        data() {
            return {
                form: {
                    username: ''
                }
            }
        },
        sockets: {
            logined() {
                this.$router.push(this.$route.query.fr || "/home")
            }
        },
        beforeRouteEnter(to, from, next) {
            if (window.localStorage.username != null) {
                next(vm => {
                    vm.$socket.emit('login', window.localStorage.username)
                })
            } else {
                next()
            }
        },
        methods: {
            login() {
                window.localStorage.username = this.form.username
                this.$socket.emit('login', this.form.username)
            }
        }
    }
</script>

<style scoped>
    .container .card {
        margin-top: 16px;
    }
</style>