import Vue from "vue";
import VueRouter from "vue-router";
import Blank from "../views/Blank.vue";
import Login from "../views/Login.vue";
const Home = r =>
  require.ensure([], () => r(require("../views/Home.vue")), "Home");
const Room = r =>
  require.ensure([], () => r(require("../views/Room.vue")), "Room");

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "blank",
    component: Blank,
    meta: {
      requireLogin: false
    }
  },
  {
    path: "/login",
    name: "login",
    component: Login,
    meta: {
      requireLogin: false
    }
  },
  {
    path: "/home",
    name: "home",
    component: Home,
    meta: {
      requireLogin: true
    }
  },
  {
    path: "/room/:id",
    name: "room",
    component: Room,
    meta: {
      requireLogin: true
    }
  }
];

const router = new VueRouter({
  routes
});

export default router;
