import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    connected: false,
    username: null,
    playList: [],
    room: null,
    nowPlaying: {
      index: -1,
      position: 0,
      item: null
    }
  },
  getters: {
    nowPlayingIndex: state => state.nowPlaying.index,
    nowPlayingItem: state =>
      state.nowPlaying.index != -1
        ? state.playList[state.nowPlaying.index]
        : null,
    nowPlayingPosition: state => state.nowPlaying.position,
    roomId: state => (state.room == null ? null : state.room.id),
    roomUserList: state => (state.room == null ? [] : state.room.users),
    roomCreater: state => (state.room == null ? null : state.room.creater)
  },
  mutations: {
    SOCKET_logined(state, data) {
      state.username = data;
    },
    SOCKET_connected(state) {
      state.connected = true;
      if (state.username != null) {
        console.log(this);
      }
    },
    SOCKET_joinedRoom(state, data) {
      state.playList = data.playList;
      state.nowPlaying = data.room.nowPlaying.room;
      state.room = data.room;
    },
    SOCKET_playListUpdated(state, data) {
      state.playList = data.playList;
      if (state.nowPlaying.index != -1) {
        state.nowPlaying.item = state.playList[state.nowPlaying.index];
      }
    },
    SOCKET_nowPlayingUpdated(state, data) {
      state.nowPlaying.index = data.index;
      state.nowPlaying.position = data.position;
      state.nowPlaying.item = state.playList[state.nowPlaying.index];
    },
    SOCKET_playing_position_updated(state, position) {
      state.nowPlaying.position = position;
    },
    SOCKET_room_user_list_updated(state, data) {
      state.room.users = data;
    }
  },
  actions: {},
  modules: {}
});
